import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import  Button  from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';


class EditCustomers extends Component{
    state = {
      customers : []
    
    }
      ChangeDetails(event)
      {
          event.preventDefault();
          axios.post(`https://localhost:44335/api/Admin/updatecustomerdetails`,
           {
               "First_name" : $('#usernameidd').valueOf().val(),
               "Last_name" : $('#passwordidd').valueOf().val(),
               "Adress" : $('#nameid').valueOf().val(),
               "Phone" : $('#emailid').valueOf().val(),
               "Credit_card_no" : $('#countryidid').valueOf().val(),
               "User_id" : parseInt($('#userid').valueOf().val())
           },
            {
              headers: {
                'Authorization': `Bearer ${localStorage["JWT"]}`
              }
            })
      }

      GetRequests()
      {
        axios.get("https://localhost:44335/api/Admin/getallcustomers",{
            headers: {
              'Authorization': `Bearer ${localStorage["JWT"]}`
            }
          })
        .then(response => {
            this.setState(this.state.customers = response.data.map(request => {
            return(
            <div key={request.id}>
            <form name="details">
              <p>First Name<input defaultValue={request.first_name} type="text" id="usernameidd" required/></p>
              <p>Last Name<input defaultValue={request.last_name} type="text" id="passwordidd" required/></p>
              <p>Adress<input defaultValue={request.adress} type="text" id="nameid" required/></p>
              <p>Phone<input defaultValue={request.phone} type="text" id="emailid" required/></p>
              <p>Credit Card Number<input defaultValue={request.credit_card_no} type="text" id="countryidid" required/></p>
              <p>User ID<input defaultValue={request.user_id} type="text" id="userid" required/></p>
              <Button onClick={this.ChangeDetails}>Edit</Button>
             </form>
             <hr/>
            </div>)
            }))
       })
      }

      componentDidMount()
      {
          this.GetRequests()
      }
      
     
     
  
        render()
    {
      return(
        <div>
           <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/AirlineRequests">AirlineRequests</NavLink></li>
                    <li><NavLink to="/EditAirlines">EditAirlines</NavLink></li>
                    <li><NavLink to="/EditCustomers">EditCustomers</NavLink></li>
                </ul>
            </div>
        </nav>
            {this.state.customers}
        </div>
     )
    }
  }
  export default EditCustomers