import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import  Button  from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';



class AirlineDetails extends Component{
  state = {
    initialUserName : "",
    initialPassword : "",
    initialName : "",
    initialEmail : "",
    initialCountryId : ""
  }
    ChangeDetails(event)
    {
        event.preventDefault();
        axios.post(`https://localhost:44335/api/Airline/updatedetails`,
         {
             "Username" : $('#usernameidd').valueOf().val(),
             "Password" : $('#passwordidd').valueOf().val(),
             "Name" : $('#nameid').valueOf().val(),
             "email" : $('#emailid').valueOf().val(),
             "CountryId" : parseInt($('#countryidid').valueOf().val())
         },
          {
            headers: {
              'Authorization': `Bearer ${localStorage["JWT"]}`
            }
          })
    }
    componentDidMount()
    {
      axios.get("https://localhost:44335/api/Airline/getmydetails", {
        headers: {
          'Authorization': `Bearer ${localStorage["JWT"]}`
        }
      })
      .then(res =>
        {
          this.setState(this.setState({initialUserName: res.data.username}))
          this.setState(this.setState({initialPassword: res.data.password}))
          this.setState(this.setState({initialName: res.data.name}))
          this.setState(this.setState({initialEmail: res.data.email}))
          this.setState(this.setState({initialCountryId: res.data.countryId}))
        })

        $.ajax({
          url:"https://localhost:44335/api/Country/getcountries"
        }).then(function(data)
        {
          var countries = $("#countryid");
          $.each(data, function(i, country)
          {
            countries.append(`<option value="${country.id}">${country.name}</option>`)
          })
    
        })
      }
    
   

    render()
  {
    return(
      <div>
        <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/Airline">Airline</NavLink></li>
                    <li><NavLink to="/AirlineDetails">AirlineDetails</NavLink></li>
                </ul>
            </div>
        </nav>
          <form name="details">
            <p>Username<input defaultValue={this.state.initialUserName} type="text" id="usernameidd" required/></p>
            <p>Password<input defaultValue={this.state.initialPassword} type="text" id="passwordidd" required/></p>
            <p>Name<input defaultValue={this.state.initialName} type="text" id="nameid" required/></p>
            <p>Email<input defaultValue={this.state.initialEmail} type="text" id="emailid" required/></p>
            <p>Country<select name="countrytxt" id="countryid"/></p>
            <Button onClick={this.ChangeDetails}>Edit</Button>
           </form>
      </div>
  )
  }
}
export default AirlineDetails