import React, { Component } from 'react'
import axios from 'axios';
import  Button  from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';


class AirlineRequests extends Component{
    state = {
        requests : [],
        countries : {}
      }
      AcceptRequest(request_id, name, countryId, userName, password, email)
      {
        axios.put(`https://localhost:44335/api/Admin/acceptairline`,
        {
            "ID" : parseFloat("0"), 
            "Name" : name, 
            "CountryID" : countryId,
            "UserName" : userName,
            "Password" : password,
            "Email" : email,
        },
        {
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
        .then(res =>
          {
            this.DenyRequest(request_id)
          })
      }

      DenyRequest(request_id)
      {
        axios.delete(`https://localhost:44335/api/Admin/denyrequest/${request_id}`,{
              headers: {
                'Authorization': `Bearer ${localStorage["JWT"]}`
              }
            })
            .then(r =>
                {
                  this.GetRequests()
                })
            
      }
      GetRequests()
      {
        axios.get("https://localhost:44335/api/Admin/getrequests",{
            headers: {
              'Authorization': `Bearer ${localStorage["JWT"]}`
            }
          })
        .then(response => {
            this.setState(this.state.requests = response.data.map(request => {
              return(
              <div key={request.id}>
                <p>Country : {this.state.countries[`${request.countryID}`]}</p>
                <p>Email : {request.email}</p>
                <p>Name : {request.name}</p>
                <p>Password : {request.password}</p>
                <p>Username : {request.userName}</p>
                <Button onClick={() => this.DenyRequest(request.id)}>Deny</Button>
                <Button onClick={() => this.AcceptRequest(request.id, request.name, request.countryID,
                  request.userName, request.password, request.email)}>Accept</Button>
                <hr/>
              </div>)
            }))
       })
      }

      GetCountries()
  {
      axios.get("https://localhost:44335/api/Country/getcountries",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(country => {
            obj[`${country.id}`] = `${country.name}`
          })
          this.setState(this.state.countries = obj)
          })
          .then(r =>
            {
              this.GetRequests()
            })
  }
      
          componentDidMount()
        {
          this.GetCountries()
        }
      render()
      {
        return(
          <div>
            <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/AirlineRequests">AirlineRequests</NavLink></li>
                    <li><NavLink to="/EditAirlines">EditAirlines</NavLink></li>
                    <li><NavLink to="/EditCustomers">EditCustomers</NavLink></li>
                </ul>
            </div>
        </nav>
              {this.state.requests}
          </div>
      )
      }
}
export default AirlineRequests