import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import  Button  from 'react-bootstrap/Button';
import { NavLink } from 'react-router-dom';


class EditAirlines extends Component{
    state = {
      customers : [],
      countries : {},
      airlines: [],
      ID : 0,
      Name : "",
      CountryID : 0,
      UserId : 0,
    }
      ChangeDetails(id, name, countryid, userid)
      { 
          axios.post(`https://localhost:44335/api/Admin/updateairlinedetails`,
           {
               "ID" : id, 
               "Name" : name,
               "CountryID" : countryid,
               "UserId" : userid
           },
            {
              headers: {
                'Authorization': `Bearer ${localStorage["JWT"]}`
              }
            })
      }

      GetAirlines()
      {
        axios.get("https://localhost:44335/api/Admin/getallairlinecompanies",{
            headers: {
              'Authorization': `Bearer ${localStorage["JWT"]}`
            }
          })
        .then(response => {
            this.setState(this.state.customers = response.data.map(request => {
            return(
            <div key={request.id}>
            <form name="details" onSubmit={e => {
            e.preventDefault();}}>
              <p>Name<input defaultValue={request.name} type="text" id="usernameidd" required/></p>
              <p>Country<input defaultValue={this.state.countries[`${request.countryID}`]} type="text" id="passwordidd" required/></p>
              <p>User ID<input defaultValue={request.userId} type="text" id="nameid" required/></p>
              <Button onClick={() => this.ChangeDetails(request.id ,request.name,
                 request.countryID, request.userId)}>Edit</Button>
             </form>
             <hr/>
            </div>)
            }))
       })
      }

      GetCountries()
  {
      axios.get("https://localhost:44335/api/Country/getcountries",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(country => {
            obj[`${country.id}`] = `${country.name}`
          })
          this.setState(this.state.countries = obj)
          })
          .then(r =>
            {
              this.GetAirlines()
            })
  }

      componentDidMount()
      {
          this.GetCountries()
      }
      
      render()
    {
      return(
        <div>
           <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/AirlineRequests">AirlineRequests</NavLink></li>
                    <li><NavLink to="/EditAirlines">EditAirlines</NavLink></li>
                    <li><NavLink to="/EditCustomers">EditCustomers</NavLink></li>
                </ul>
            </div>
        </nav>
            {this.state.customers}
        </div>
     )
    }
  }
  export default EditAirlines