import './App.css';
import React, {Component} from 'react';
import axios from 'axios';
import $ from 'jquery';
import base64 from 'react-native-base64'
import { Route, BrowserRouter} from 'react-router-dom';
import Navbar from './components/Navbar';
import MyTickets from './components/MyTickets';
import PersonalDetails from './components/PersonalDetails';
import Airline from './components/Airline';
import AirlineDetails from './components/AirlineDetails';
import AirlineRequests from './components/AirlineRequests';
import EditAirlines from './components/EditAirlines';
import Flights from './components/Flights';
import  Button  from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css'
import Login from './components/Login';
import AirlineDashboard from './components/AirlineDashboard.';
import AdminDashboard from './components/AdminDashboard';
import CustomerDashboard from './components/CustomerDashboard';
import EditCustomers from './components/EditCustomers';
import AnonymousUser from './components/AnonymousUser';
//import $, { event } from 'jquery';
//import LogIn from './Login'


class App extends Component {

  render() {

    return (
    <div className="App">
    <BrowserRouter>
    <div className="App">
      <header className="App">
        <Route exact path="/" component={Login} />
        <Route path="/MyTickets" component={MyTickets} />
        <Route path="/PersonalDetails" component={PersonalDetails} />
        <Route path="/Airline" component={Airline} />
        <Route path="/AirlineDetails" component={AirlineDetails} />
        <Route path="/AirlineRequests" component={AirlineRequests} />
        <Route path="/EditAirlines" component={EditAirlines} />
        <Route path="/Flights" component={Flights} />
        <Route path="/AirlineDashboard" component={AirlineDashboard}/>
        <Route path="/AdminDashboard" component={AdminDashboard}/>
        <Route path="/CustomerDashboard" component={CustomerDashboard}/>
        <Route path="/EditCustomers" component={EditCustomers}/>
        <Route path="/AnonymousUser" component={AnonymousUser}/>
      </header>
    </div>
    </BrowserRouter>
    </div>
      );
    }
}

export default App;

