import React, { Component } from 'react'
import axios from 'axios';
import  Button  from 'react-bootstrap/Button';
import swal from 'sweetalert';
import { NavLink } from 'react-router-dom';


class MyTickets extends Component{
  state = {
    flights : [],
    countries : {},
    airlines: {}
  }
  cancleTicket(flight_id)
  {
    axios.get(`https://localhost:44335/api/Customer/getflight/${flight_id}`,{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
        .then(result =>
          {
            if(Date.parse(result.data.departureTime) < new Date())
            {
              console.log("that flight have already left")
              swal("you cant cancle a ticket of a flight that already left");

            }
            else
            {
              axios.get(`https://localhost:44335/api/Customer/GetMyTicketByFlight/${flight_id}`, {
                headers: {
                  'Authorization': `Bearer ${localStorage["JWT"]}`
                }
              })
              .then(res =>
                {
                  axios.post(`https://localhost:44335/api/Customer/cancleticket/${res.data.id}`,"",{
                  headers: {
                    'Authorization': `Bearer ${localStorage["JWT"]}`
                  }
                })
                .then(r =>
                  {
                    this.getMyTickets()
                  })
                })
            }
          }) 
  }

  getMyTickets()
  {
    axios.get("https://localhost:44335/api/Customer/getAllmyflights",{
        headers: {
          'Authorization': `Bearer ${localStorage["JWT"]}`
        }
      })
    .then(response => {
        var x = 0;
        this.setState(this.state.flights = response.data.map(flight => {
          x++;
          return(
          <div key={x}>
            <p>Airline Company : {this.state.airlines[flight.airlineCompanyID]}</p>
            <p>Origin Country : {this.state.countries[`${flight.originCountryID}`]}</p>
            <p>Destination Country : {this.state.countries[`${flight.destinationCountryID}`]}</p>
            <p>Departure Time : {flight.departureTime}</p>
            <p>Landing Time : {flight.landingTime}</p>
            <p>Remaining Tickets : {flight.remainingTickets}</p>
            <Button onClick={() => this.cancleTicket(flight.id)}>cancle ticket</Button>
            <hr/>
          </div>)
        }))
   })
  }

  GetCountries()
  {
      axios.get("https://localhost:44335/api/Country/getcountries",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(country => {
            obj[`${country.id}`] = `${country.name}`
          })
          this.setState(this.state.countries = obj)
          })
          .then(r =>
            {
              this.getMyTickets()
            })
  }

  GetAirlines()
  {
      axios.get("https://localhost:44335/api/Customer/getallairlinecompanies",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(airline => {
            obj[`${airline.id}`] = `${airline.name}`
          })
          this.setState(this.state.airlines = obj)
          })
          .then(this.GetCountries())
  }


      componentDidMount()
    {
      this.GetAirlines()
    }
  render()
  {
    return(
      <div>
        <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/MyTickets">MyTickets</NavLink></li>
                    <li><NavLink to="/PersonalDetails">PersonalDetails</NavLink></li>
                    <li><NavLink to="/Flights">Flights</NavLink></li>
                </ul>
            </div>
        </nav>
          {this.state.flights}
      </div>
  )
  }
}
export default MyTickets