import React, { Component } from 'react'
import axios from 'axios';
import $ from 'jquery';
import  Button  from 'react-bootstrap/Button';
import swal from 'sweetalert';
import { NavLink } from 'react-router-dom';
import base64 from 'react-native-base64'



class Airline extends Component{
    state = {
        myFlights : [],
        countries : {}
    }

    CancleFlight(flight_id)
  {
    axios.get(`https://localhost:44335/api/Airline/getflight/${flight_id}`,{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
        .then(res =>
          {
            if(Date.parse(res.data.departureTime) < new Date())
            {
              swal("you cant cancle a flight that already left");
            }
            else
            {
              axios.delete(`https://localhost:44335/api/Airline/cancleflight/${flight_id}`,{
              headers: {
                'Authorization': `Bearer ${localStorage["JWT"]}`
              }
            })
            .then(r =>
                {
                  this.GetMyFlights()
                })
            }
          }) 
  }

  AddFlight()
  {
    axios.put(`https://localhost:44335/api/Airline/createflight`,
    {
        "ID" : parseFloat("0"), 
        "AirlineCompanyID" : parseFloat(JSON.parse("{" + String(base64.decode((String(`${localStorage["JWT"]}`).split(".")[1])).split(",")[5]) + "}")["idd"]), 
        "OriginCountryID" : parseInt($('#originCountryID').valueOf().val()),
        "DestinationCountryID" : parseInt($('#destinationCountryID').valueOf().val()),
        "DepartureTime" : $('#departuretimeid').valueOf().val(),
        "LandingTime" : $('#landingimeid').valueOf().val(),
        "RemainingTickets" : parseInt($('#remainingticketsid').valueOf().val())
    },
     {
       headers: {
         'Authorization': `Bearer ${localStorage["JWT"]}`
       }
     })     
  }

  GetCountries()
  {
      axios.get("https://localhost:44335/api/Country/getcountries",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(country => {
            obj[`${country.id}`] = `${country.name}`
          })
          this.setState(this.state.countries = obj)
          })
          .then(r =>
            {
              this.GetMyFlights()
            })
  }


    GetMyFlights()
    {
      axios.get("https://localhost:44335/api/Airline/getallcompanyflights",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          this.setState(this.state.myFlights = response.data.map(flight => {
            return(
            <div key={flight.id}>
              <p>Airline Company : {JSON.parse("{" + String(base64.decode((String(`${localStorage["JWT"]}`).split(".")[1])).split(",")[6]) + "}")["name"]}</p>
              <p>Origin Country : {this.state.countries[`${flight.originCountryID}`]}</p>
              <p>Destination Country : {this.state.countries[`${flight.destinationCountryID}`]}</p>
              <p>Departure Time : {flight.departureTime}</p>
              <p>Landing Time : {flight.landingTime}</p>
              <p>Remaining Tickets : {flight.remainingTickets}</p>
              <Button onClick={() => this.CancleFlight(flight.id)}>Cancle Flight</Button>
              <hr/>
            </div>)
          }))
     })
    }

    componentDidMount()
    {
      
      this.GetCountries()
      $.ajax({
        url:"https://localhost:44335/api/Country/getcountries"
      }).then(function(data)
      {
        var orgCountries = $("#originCountryID");
        var dstCountries = $("#destinationCountryID");
        $.each(data, function(i, country)
        {
          orgCountries.append(`<option value="${country.id}">${country.name}</option>`)
          dstCountries.append(`<option value="${country.id}">${country.name}</option>`)
        })
      })
      axios.get("https://localhost:44335/api/Airline/getallairlinecompanies",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(res => 
      {
        var airlinecomp = $("#airlinecomp");
        $.each(res.data, function(i, airline)
        {
          airlinecomp.append(`<option value="${airline.id}">${airline.name}</option>`)
        })
      })
    }

    render()
    {
        return(
            <div>
              <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/Airline">Airline</NavLink></li>
                    <li><NavLink to="/AirlineDetails">AirlineDetails</NavLink></li>
                </ul>
            </div>
        </nav>
               {this.state.myFlights}
               <div>
                    <form name="details" onSubmit={e => {
            e.preventDefault();}}>
                 <p>Origin Country <select name="orgCountry" id="originCountryID"/></p>
                 <p>Destination Country <select name="destCountry" id="destinationCountryID"/></p>
                 <p>Departure Time<input type="date" id="departuretimeid" required/></p>
                 <p>Landing Time<input type="date" id="landingimeid" required/></p>
                 <p>Remaining Tickets<input type="text" id="remainingticketsid" required/></p>
                 <Button onClick={this.AddFlight}>Add Flight</Button>
                 </form>
               </div>
            </div>
        )
    }
}
export default Airline