import React, { Component } from 'react'
import axios from 'axios';
import  Button  from 'react-bootstrap/Button';
import swal from 'sweetalert';
import { NavLink } from 'react-router-dom';
import $ from 'jquery';



class AnonymousUser extends Component{
    state = {
        Flights : [],
        countries : {},
        airlines: {}
    }

    PurchaseTicket()
  {
    swal("you need to log in order to purchase a ticket");
  }
  

    GetFlights()
    {
      axios.get("https://localhost:44335/api/Anonymous/getallflights",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          this.setState(this.state.Flights = response.data.map(flight => {
            return(
            <div key={flight.id}>
              <p>Airline Company : {this.state.airlines[flight.airlineCompanyID]}</p>
              <p>Origin Country : {this.state.countries[`${flight.originCountryID}`]}</p>
              <p>Destination Country : {this.state.countries[`${flight.destinationCountryID}`]}</p>
              <p>Departure Time : {flight.departureTime}</p>
              <p>Landing Time : {flight.landingTime}</p>
              <p>Remaining Tickets : {flight.remainingTickets}</p>
              <Button onClick={this.PurchaseTicket}>Purchase Ticket</Button>
              <hr/>
            </div>)
          }))
     })
    }

    GetCountries()
  {
      axios.get("https://localhost:44335/api/Country/getcountries",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(country => {
            obj[`${country.id}`] = `${country.name}`
          })
          this.setState(this.state.countries = obj)
          })
          .then(r =>
            {
              this.GetFlights()
            })
  }
  GetAirlines()
  {
      axios.get("https://localhost:44335/api/Anonymous/getallairlinecompanies",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(response => {
          var obj = {}
          response.data.forEach(airline => {
            obj[`${airline.id}`] = `${airline.name}`
          })
          this.setState(this.state.airlines = obj)
          })
  }
  constructor(props)
  {
    super(props)
    this.GetAirlines()
  }

    componentDidMount()
    {
      this.GetCountries()
      $.ajax({
        url:"https://localhost:44335/api/Country/getcountries"
      }).then(function(data)
      {
        var orgCountries = $("#originCountryID");
        var dstCountries = $("#destinationCountryID");
        $.each(data, function(i, country)
        {
          orgCountries.append(`<option value="${country.id}">${country.name}</option>`)
          dstCountries.append(`<option value="${country.id}">${country.name}</option>`)
        })
      })
      axios.get("https://localhost:44335/api/Anonymous/getallairlinecompanies",{
          headers: {
            'Authorization': `Bearer ${localStorage["JWT"]}`
          }
        })
      .then(res => 
      {
        var airlinecomp = $("#airlinecomp");
        $.each(res.data, function(i, airline)
        {
          airlinecomp.append(`<option value="${airline.id}">${airline.name}</option>`)
        })
      })
    }

    render()
    {
        return(
            <div>
              <nav>
            <div>
                <a>CoolFlightsPlace</a>
                <ul>
                    <li><NavLink to="/MyTickets">MyTickets</NavLink></li>
                    <li><NavLink to="/PersonalDetails">PersonalDetails</NavLink></li>
                    <li><NavLink to="/Flights">Flights</NavLink></li>
                </ul>
            </div>
        </nav>
               <p>Origin Country <select name="orgCountry" id="originCountryID"/></p>
               <p>Destination Country <select name="destCountry" id="destinationCountryID"/></p>
               <p>Departure Time<input type="date" id="departuretimeid" required/></p>
               <p>Landing Time<input type="date" id="landingimeid" required/></p>
               <p>Airline Company <select name="airline" id="airlinecomp"/></p>
               {/* i had a problem with using "this" so i made the function here... */}
               <Button onClick={() => 
               axios.get("https://localhost:44335/api/Anonymous/getallflights",{
                headers: {
                  'Authorization': `Bearer ${localStorage["JWT"]}`
                }
              })
            .then(response => {
                this.setState(this.state.Flights = response.data.map(flight => {
                  if(
                  flight.originCountryID == parseInt($('#originCountryID').valueOf().val()) &&
                  flight.airlineCompanyID == parseInt($('#airlinecomp').valueOf().val()) &&
                  flight.destinationCountryID == parseInt($('#destinationCountryID').valueOf().val()) &&
                  ($('#departuretimeid').valueOf().val().length == 0 ||
                  (Date.parse($('#departuretimeid').valueOf().val()) - 10800000 <= Date.parse(flight.departureTime)) && 
                  Date.parse($('#departuretimeid').valueOf().val()) + 75600000 >= Date.parse(flight.departureTime)) &&
                  ($('#landingimeid').valueOf().val().length == 0 ||
                  (Date.parse($('#landingimeid').valueOf().val()) - 10800000 <= Date.parse(flight.landingTime) && 
                  Date.parse($('#landingimeid').valueOf().val()) + 75600000 >= Date.parse(flight.landingTime))))
                  {
                    return(
                      <div key={flight.id}>
                        <p>Airline Company : {this.state.airlines[flight.airlineCompanyID]}</p>
                        <p>Origin Country : {this.state.countries[`${flight.originCountryID}`]}</p>
                        <p>Destination Country : {this.state.countries[`${flight.destinationCountryID}`]}</p>
                        <p>Departure Time : {flight.departureTime}</p>
                        <p>Landing Time : {flight.landingTime}</p>
                        <p>Remaining Tickets : {flight.remainingTickets}</p>
                        <Button onClick={this.PurchaseTicket}>Purchase Ticket</Button>
                        <hr/>
                      </div>)
                  }
                  
                }))
           })
              }>Search </Button>
               {this.state.Flights}
            </div>
        )
    }
}
export default AnonymousUser