import React, { Component } from 'react'
import axios from 'axios';
import  Button  from 'react-bootstrap/Button';
import $ from 'jquery';
import base64 from 'react-native-base64'
import {Redirect, useHistory} from 'react-router-dom'
import swal from 'sweetalert';

class Login extends Component{

  state = {
    redirect: 'none'
  }

  redirect(role) {
    this.setState({
      redirect: role
    });
  }

  tryLogIn = () => {
    const obj = {
        Name: $('#usernameid').valueOf().val(),
        Password: $('#passwordid').valueOf().val()
    }
    axios.post("https://localhost:44335/api/Auth/token", obj)
    .then(response => {
      localStorage.setItem('JWT',response.data)
      if("administrator" === JSON.parse("{" + String(base64.decode((String(response.data).split(".")[1])).split(",")[4]) + "}")["roletype"])
      {
        this.redirect('admin')
      }

      if("customer" === JSON.parse("{" + String(base64.decode((String(response.data).split(".")[1])).split(",")[4]) + "}")["roletype"])
      {
        this.redirect('customer')
      }

      if("airlinecompany" === JSON.parse("{" + String(base64.decode((String(response.data).split(".")[1])).split(",")[4]) + "}")["roletype"]) 
      {
        this.redirect('airline')
      }
      localStorage.setItem('Role', JSON.parse("{" + String(base64.decode((String(response.data).split(".")[1])).split(",")[4]) + "}")["roletype"])

    }).catch(error => {
      swal("wrong username or password please try again");
    })
}

  ContinueAsGuest = () => {
    axios.post("https://localhost:44335/api/Auth/token", 
    {
      Name: "justATokenForAnonymousUsers528379028750",
      Password: "k"
    })
    .then(response => {
      localStorage.setItem('JWT',response.data)
    })
    .then(this.redirect('anonymous'))
  }
  Register = () => {
    
    //this.redirect('register')
    window.location.href = 'Html_Pages/CustomerSignUp.html'
  }
  render()
  {
    return(
      <div onSubmit={e => {
        e.preventDefault();}}>
          <form>
        <p>Username<input type="text" id="usernameid" required/></p>
        <p>Password<input type="text" id="passwordid" required/></p>
        <Button onClick={this.tryLogIn}>sign in</Button>
        {this.state.redirect == 'airline' &&
          <Redirect to='/AirlineDashboard'/> ||
          this.state.redirect == 'customer' &&
          <Redirect to='/CustomerDashboard'/> ||
          this.state.redirect == 'admin' &&
          <Redirect to='/AdminDashboard'/>
        }
        <p/>
        <Button onClick={this.ContinueAsGuest}>continue as guest</Button>
        {this.state.redirect == 'anonymous' &&
          <Redirect to='/AnonymousUser'/>
        }
        <Button onClick={this.Register}>register</Button>
        {this.state.redirect == 'register' &&
          <Redirect to='~/Html_Pages/CustomerSignUp'/>
        }
      </form>
      </div>
  );
  }
}
export default Login